import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PaginaCoachComponent } from './pagina-coach.component';

const routes: Routes = [
  { path: '', component: PaginaCoachComponent },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class PaginaCoachRoutingModule { }
